    export default {
        data() {
            return {
                callValue: 0,    // (待我审批的/我已审批的)切换值
                value: "",   //  搜索框默认值
                // 状态列表
                stateList: ['全部','未审核','通过','拒绝','撤销'],
                // 类型列表
                typeList: ['全部','请假','外出','出差','加班','换班','补卡'],
                // 待我审批列表
                causeList: [],
                page: 1,            //  呼叫列表请求页码页数
                maxPage: 1,
                limit: 10,
                loadText: '下拉加载更多',        //  呼叫中列表  下拉提示词
                loading: true,                  //   呼叫中列表   下拉圈圈
                onFetching: false,
                show: false,  // 判断筛选弹窗显示或隐藏
                company_name: "",  // 类型
                setCenter: false,  //设置中心弹窗
                typeNum: 0,       //  待我审批类型值
                overTypeNum: 0,       //  已审批类型值
                sendNum: 0,     //   已抄送我的状态值
                sendTypeNum: 0,     //   已抄送我的类型值
                nums: 1,    //  传后端的选项卡判断
            }
        },
        mounted(){
            this.getInfo();  //  数据初始化
        },
        methods:{
            getInfo(){
                if (this.onFetching) {
                    // do nothing
                } else {
                    this.onFetching = true;
                    if(this.page > this.maxPage){
                        this.onFetching = false;
                        return;
                    }
                    this.common.commonFun.ajax({
                        dom: this,
                        url: this.common.apiUrl.examine,
                        type: "post",
                        data: {
                            page: this.page,
                            limit: this.limit,
                            top: this.nums,
                        },
                        success: (response, allResponse) => {
                            // console.log('response',response)
                            this.onFetching = true;
                            this.maxPage = Math.ceil(response.count/this.limit) || 1;
                            if(this.page >= this.maxPage){
                                this.onFetching = false;
                                console.log('滚动结束');
                                this.loadText = "已经到底了哦~";
                                this.loading = false;
                            }
                            this.causeList = this.causeList.concat(response.list);
                            this.onFetching = false;
                            this.page ++;
                        }
                    });
                }
            },
            // 搜索中确定按钮
            sure(){
                console.log('我要搜索了',this.value)
                this.causeList = [];
                this.$refs.scrollerBottom.reset({top: 0});
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.examine,
                    type: "post",
                    data: {
                        page: 1,
                        limit: 100,
                        top: this.nums,
                        code: this.value,
                    },
                    success: (response, allResponse) => {
                        // console.log('response',response)
                        this.causeList = response.list
                    }
                });
            },
            delet(){
                this.value = ''
            },
            //  判断(我已审批中)选择的类型值
            overType(index){
                this.overTypeNum = index
                console.log(this.overTypeNum)
            },
            // 判断(待我审批中)选择的类型值
            gainType(index){
                this.typeNum = index
            },
            // 判断(已抄送我的)选择的状态值
            sendState(index){
                this.sendNum = index
            },
            // 已抄送我的状态值
            sendType(index){
                this.sendTypeNum = index
            },
            // 我已审批提交按钮
            overBaocun(){
                this.setCenter = false;
                this.causeList = [];
                this.$refs.scrollerBottom.reset({top: 0});
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.examine,
                    type: "post",
                    data: {
                        page: 1,
                        limit: 100,
                        top: 2,
                        type: this.overTypeNum,
                    },
                    success: (response, allResponse) => {
                        // console.log('response',response)
                        this.causeList = response.list
                    }
                });
            },
            // 待我审批提交保存按钮
            waitBaocun(){
                this.setCenter = false;
                this.causeList = [];
                this.$refs.scrollerBottom.reset({top: 0});
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.examine,
                    type: "post",
                    data: {
                        page: 1,
                        limit: 100,
                        top: 1,
                        type: this.typeNum,
                    },
                    success: (response, allResponse) => {
                        // console.log('response',response)
                        this.causeList = response.list
                    }
                });
            },
            // 已抄送我的保存按钮
            sendBaocun(){
                this.setCenter = false;
                this.causeList = [];
                this.$refs.scrollerBottom.reset({top: 0});
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.examine,
                    type: "post",
                    data: {
                        page: 1,
                        limit: 100,
                        top: 3,
                        type: this.sendTypeNum,
                        status: this.sendNum - 1,
                    },
                    success: (response, allResponse) => {
                        // console.log('response',response)
                        this.causeList = response.list
                    }
                });
            },
//            头部导航箭头按钮
            backHome(){
                this.$router.push("/manage")
            },
//            (待我审批的/我已审批的)切换值按钮
            onItemClick (index) {
                if( index == 0){
                    this.nums = 1;
                }
                if( index == 1 ){
                    this.nums = 2;
                }
                if( index == 2 ){
                    this.nums = 3;
                }
                this.causeList = [];
                this.$refs.scrollerBottom.reset({top: 0});
                this.page = 1;
                this.maxPage = 1;
                this.loadText = '下拉加载更多';
                this.loading = true;
                this.getInfo();
            },
//            根据不同下标请求不同的页面
            whoask(index,username){
                console.log("我点击的是第",index)
                this.$router.push({
                    name: 'staff',
                    params:{
                        index: index,
                        title: username
                    }
                })
            },
//            筛选弹窗确定按钮
            choice(){
                console.log("类型选好了,我可能要请求数据了,我选择的是",this.company_name)
                this.show = false;
            },
        }
    }



